<template><div class="markdown-body"><h1>滑块</h1>
<p><code v-pre="">mu-slider</code> 用于在一个固定区间内进行选择。</p>
<h2 id="shi-li"><a class="header-anchor" href="#shi-li" aria-hidden="true">¶</a> 示例</h2>
<demo-block :jsfiddle="{&quot;html&quot;:&quot;&lt;mu-container&gt;\n  &lt;mu-slider class=\&quot;demo-slider\&quot; v-model=\&quot;normal.value1\&quot;&gt;&lt;/mu-slider&gt;\n  &lt;mu-slider class=\&quot;demo-slider\&quot; :step=\&quot;10\&quot; v-model=\&quot;normal.value2\&quot;&gt;&lt;/mu-slider&gt;\n  &lt;mu-slider class=\&quot;demo-slider\&quot; disabled v-model=\&quot;normal.value3\&quot;&gt;&lt;/mu-slider&gt;\n&lt;/mu-container&gt;\n&quot;,&quot;script&quot;:null,&quot;style&quot;:null}">
                    <div class="source" slot="source"><mu-container>
  <mu-slider class="demo-slider" v-model="normal.value1"></mu-slider>
  <mu-slider class="demo-slider" :step="10" v-model="normal.value2"></mu-slider>
  <mu-slider class="demo-slider" disabled v-model="normal.value3"></mu-slider>
</mu-container>
</div>
                    
                    <div class="highlight" slot="highlight"><pre v-pre="" class="hljs"><code v-pre=""><span class="hljs-tag">&lt;<span class="hljs-name">mu-container</span>&gt;</span>
  <span class="hljs-tag">&lt;<span class="hljs-name">mu-slider</span> <span class="hljs-attr">class</span>=<span class="hljs-string">"demo-slider"</span> <span class="hljs-attr">v-model</span>=<span class="hljs-string">"normal.value1"</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">mu-slider</span>&gt;</span>
  <span class="hljs-tag">&lt;<span class="hljs-name">mu-slider</span> <span class="hljs-attr">class</span>=<span class="hljs-string">"demo-slider"</span> <span class="hljs-attr">:step</span>=<span class="hljs-string">"10"</span> <span class="hljs-attr">v-model</span>=<span class="hljs-string">"normal.value2"</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">mu-slider</span>&gt;</span>
  <span class="hljs-tag">&lt;<span class="hljs-name">mu-slider</span> <span class="hljs-attr">class</span>=<span class="hljs-string">"demo-slider"</span> <span class="hljs-attr">disabled</span> <span class="hljs-attr">v-model</span>=<span class="hljs-string">"normal.value3"</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">mu-slider</span>&gt;</span>
<span class="hljs-tag">&lt;/<span class="hljs-name">mu-container</span>&gt;</span>
</code></pre>
</div></demo-block>
<h2 id="se-cai"><a class="header-anchor" href="#se-cai" aria-hidden="true">¶</a> 色彩</h2>
<p>可以使用 <code v-pre="">color</code> 、<code v-pre="">track-color</code> 和 <code v-pre="">thumb-color</code> 属性来设置滑块的颜色。</p>
<demo-block :jsfiddle="{&quot;html&quot;:&quot;&lt;mu-container&gt;\n  &lt;mu-slider class=\&quot;demo-slider\&quot; color=\&quot;secondary\&quot; v-model=\&quot;color.value1\&quot;&gt;&lt;/mu-slider&gt;\n  &lt;mu-slider class=\&quot;demo-slider\&quot; track-color=\&quot;green\&quot; v-model=\&quot;color.value2\&quot;&gt;&lt;/mu-slider&gt;\n  &lt;mu-slider class=\&quot;demo-slider\&quot; thumb-color=\&quot;orange\&quot; v-model=\&quot;color.value3\&quot;&gt;&lt;/mu-slider&gt;\n&lt;/mu-container&gt;\n&quot;,&quot;script&quot;:null,&quot;style&quot;:null}">
                    <div class="source" slot="source"><mu-container>
  <mu-slider class="demo-slider" color="secondary" v-model="color.value1"></mu-slider>
  <mu-slider class="demo-slider" track-color="green" v-model="color.value2"></mu-slider>
  <mu-slider class="demo-slider" thumb-color="orange" v-model="color.value3"></mu-slider>
</mu-container>
</div>
                    
                    <div class="highlight" slot="highlight"><pre v-pre="" class="hljs"><code v-pre=""><span class="hljs-tag">&lt;<span class="hljs-name">mu-container</span>&gt;</span>
  <span class="hljs-tag">&lt;<span class="hljs-name">mu-slider</span> <span class="hljs-attr">class</span>=<span class="hljs-string">"demo-slider"</span> <span class="hljs-attr">color</span>=<span class="hljs-string">"secondary"</span> <span class="hljs-attr">v-model</span>=<span class="hljs-string">"color.value1"</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">mu-slider</span>&gt;</span>
  <span class="hljs-tag">&lt;<span class="hljs-name">mu-slider</span> <span class="hljs-attr">class</span>=<span class="hljs-string">"demo-slider"</span> <span class="hljs-attr">track-color</span>=<span class="hljs-string">"green"</span> <span class="hljs-attr">v-model</span>=<span class="hljs-string">"color.value2"</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">mu-slider</span>&gt;</span>
  <span class="hljs-tag">&lt;<span class="hljs-name">mu-slider</span> <span class="hljs-attr">class</span>=<span class="hljs-string">"demo-slider"</span> <span class="hljs-attr">thumb-color</span>=<span class="hljs-string">"orange"</span> <span class="hljs-attr">v-model</span>=<span class="hljs-string">"color.value3"</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">mu-slider</span>&gt;</span>
<span class="hljs-tag">&lt;/<span class="hljs-name">mu-container</span>&gt;</span>
</code></pre>
</div></demo-block>
<h2 id="slider-props"><a class="header-anchor" href="#slider-props" aria-hidden="true">¶</a> Slider Props</h2>
<table>
<thead>
<tr>
<th>参数</th>
<th>介绍</th>
<th>类型</th>
<th>可选值</th>
<th>默认值</th>
</tr>
</thead>
<tbody>
<tr>
<td>value</td>
<td>当前值,支持 <code v-pre="">v-model</code> 指令</td>
<td>Number</td>
<td>—</td>
<td>—</td>
</tr>
<tr>
<td>max</td>
<td>最大值</td>
<td>Number</td>
<td>—</td>
<td>100</td>
</tr>
<tr>
<td>min</td>
<td>最小值</td>
<td>Number</td>
<td>—</td>
<td>0</td>
</tr>
<tr>
<td>step</td>
<td>步长</td>
<td>Number</td>
<td>—</td>
<td>0.1</td>
</tr>
<tr>
<td>color</td>
<td>颜色</td>
<td>String</td>
<td>—</td>
<td>—</td>
</tr>
<tr>
<td>thumb-color</td>
<td>拇指颜色</td>
<td>String</td>
<td>—</td>
<td>—</td>
</tr>
<tr>
<td>track-color</td>
<td>填充色彩</td>
<td>String</td>
<td>—</td>
<td>—</td>
</tr>
<tr>
<td>disabled</td>
<td>是否禁用</td>
<td>Boolean</td>
<td>—</td>
<td>—</td>
</tr>
<tr>
<td>display-value</td>
<td>是否显示当前值</td>
<td>Boolean</td>
<td>—</td>
<td>—</td>
</tr>
</tbody>
</table>
<h2 id="slider-change"><a class="header-anchor" href="#slider-change" aria-hidden="true">¶</a> Slider change</h2>
<table>
<thead>
<tr>
<th>名称</th>
<th>介绍</th>
<th>回调参数</th>
</tr>
</thead>
<tbody>
<tr>
<td>change</td>
<td>拖动滑块改变值时触发事件</td>
<td>(value)</td>
</tr>
</tbody>
</table>


</div class="markdown-body"></template>
<style>
.demo-slider {
  margin: 16px 0;
}
</style>
<script>
export default {
  data () {
    return {
      normal: {
        value1: 10,
        value2: 50,
        value3: 80
      },
      color: {
        value1: 10,
        value2: 50,
        value3: 80
      }
    }
  }
}
</script>


